import { onAppointmentComposeHandler } from "./commands/appointment";
import { onMessageComposeHandler } from "./commands/message";
import i18n from "./i18n";

export function getLanguage() {
  let displayLanguage = Office.context.displayLanguage;
  const language = displayLanguage?.includes("en") ? "en" : "zh";
  return language;
}

export function setLanguage(language?: string) {
  language = language || getLanguage();
  i18n.changeLanguage(language, () => {
    // console.log("language changed", language);
    initEvents();
  });
}

export const initEvents = () => {
  const mailboxItem = Office.context?.mailbox?.item;
  if (mailboxItem) {
    if (mailboxItem.itemType === "message") {
      onMessageComposeHandler(null, true);
    }

    if (mailboxItem.itemType === "appointment") {
      onAppointmentComposeHandler(null, true);
    }
  }
};

export function showInfoBar() {
  const options = { asyncContext: { callingEvent: event } };
  if (!i18n?.t) return;
  // requirement 1.3
  Office.context.mailbox.item.notificationMessages.addAsync(
    "showInfoBarForSampleInstructions",
    {
      type: Office.MailboxEnums.ItemNotificationMessageType.InsightMessage, // requirement 1.10
      message: i18n?.t(""),
      icon: "Icon.16x16",
      actions: [
        {
          // The text of the action link.
          actionText: i18n?.t(""),
          actionType: Office.MailboxEnums.ActionType.ShowTaskPane, // requirement 1.10
          // The button defined in the manifest.
          commandId: "msgComposeOpenPaneButton",
          // JSON
          contextData: "{''}",
        },
      ],
    },
    options,
    function (result) {
      console.log("onMessageComposeHandler(): Notification message added");
      result.asyncContext.callingEvent?.completed?.();
    }
  );
}

// seriesId
export function getSeriesId() {
  // requirement 1.7
  const seriesId = Office.context.mailbox.item.seriesId;

  if (seriesId === undefined) {
    console.log("This is a message that's not a meeting request.");
  } else if (seriesId === null) {
    console.log("This is a single appointment, a parent series, or a meeting request for a series or single meeting.");
  } else {
    console.log("This is an instance belonging to series with ID " + seriesId);
  }
}

// Gets the item class of the item and displays it in the info bar.
export function getItemClass(event) {
  // requirement 1.1
  var itemClass = Office.context.mailbox.item.itemClass;

  // // requirement 1.3
  Office.context.mailbox.item.notificationMessages.addAsync("itemClass", {
    type: "informationalMessage",
    icon: "red-icon-16",
    message: "Item Class: " + itemClass,
    persistent: false,
  });

  event.completed();
}

// Gets the date and time when the item was created and displays it in the info bar.
export function getDateTimeCreated() {
  const dateTimeCreated = Office.context.mailbox.item.dateTimeCreated;
  return dateTimeCreated?.toLocaleString?.();
}

export function getItemId(itemId: string) {
  // requirement 1.1
  if (Office.context.mailbox.diagnostics.hostName === "OutlookIOS") {
    // itemId is already REST-formatted.
    return itemId;
  } else {
    // requirement 1.3: Convert to an item ID for API v2.0.
    return Office.context.mailbox.convertToRestId(itemId, Office.MailboxEnums.RestVersion.v2_0);
  }
}

// Gets the ID of the item and displays it in the info bar.
export function getItemRestId(): Promise<string> {
  // @ts-ignore
  return new Promise((resolve, reject) => {
    const itemId = Office.context.mailbox.item.itemId;
    if (itemId === null || itemId == undefined) {
      // requirement 1.3: Office.context.mailbox.item.saveAsync
      Office.context.mailbox.item.saveAsync(function (result) {
        if (result.value) {
          resolve(result.value);
        }
      });
    } else {
      resolve(itemId);
    }
  });
}

export function getCurrentItem(accessToken: string, itemId: string, itemType: string): Promise<any> {
  return new Promise((resolve, reject) => {
    // Construct the REST URL to the current item.
    // Details for formatting the URL can be found at
    // https://learn.microsoft.com/previous-versions/office/office-365-api/api/version-2.0/mail-rest-operations#get-messages.
    const type = itemType === "events" ? "events" : "messages";

    // requirement 1.5
    const getMessageUrl = Office.context.mailbox.restUrl + "/v2.0/me/" + type + "/" + itemId;
    fetch(getMessageUrl, {
      headers: { Authorization: "Bearer " + accessToken },
    })
      .then((res) => res.json())
      .then((data) => {
        resolve(data);
      })
      .catch((error) => reject(error));
  });
}

//  token
export function getToken(): Promise<string> {
  return new Promise((resolve, reject) => {
    // 
    // requirement 1.5
    Office.context.mailbox.getCallbackTokenAsync({ isRest: true }, function (result: any) {
      if (result.status === "succeeded") {
        resolve(result.value);
      } else {
        reject(result.error);
      }
    });
  });
}

// /
export function getBodyAsync(): Promise<string> {
  return new Promise((resolve, reject) => {
    // requirement 1.3
    Office.context.mailbox.item.body.getAsync(Office.CoercionType.Html, (result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve(result.value);
      } else {
        reject(result.error);
      }
    });
  });
}

// 
export function getSubjectAsync(): Promise<string> {
  const mailboxItem = Office.context.mailbox.item;
  return new Promise((resolve, reject) => {
    // requirement 1.1
    mailboxItem.subject.getAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve(result.value);
      } else {
        reject(result.error);
      }
    });
  });
}

// /
export function getAttachmentsAsync(): Promise<any> {
  return new Promise((resolve, reject) => {
    const attachments = Office.context.mailbox.item.attachments;
    if (attachments) {
      resolve(attachments);
    } else {
      reject(new Error("No attachments found"));
    }
  });
}

// 
export function hasAttachments() {
  // requirement 1.1
  const mail = Office.context.mailbox.item;
  if (mail.attachments) {
    return true;
  } else {
    return false;
  }
}

// URL
export function queryURLParams(URL: string) {
  let url = URL.split("?")[1];
  const urlSearchParams = new URLSearchParams(url);
  const params = Object.fromEntries(urlSearchParams.entries());
  return params;
}

/**
 * base64
 */
export function convertImagesToBase64(body: string) {
  //  DOM parser 
  const parser = new DOMParser();
  const doc = parser.parseFromString(body, "text/html");

  // 
  const images = doc.querySelectorAll("img");
  images.forEach((image) => {
    //  src src
    let src = image.getAttribute("src");

    //  data:image 
    if (src.indexOf("data:image") > -1) return;

    src = decodeURIComponent(src);
    const srcParams = queryURLParams(src);

    const attachmentMapStr = window.localStorage.getItem("attachmentMap");
    const attachmentMap = attachmentMapStr ? JSON.parse(attachmentMapStr) : {};
    // console.log("attachmentMap", attachmentMap);

    if (attachmentMap[`${srcParams.id}`]) {
      const base64str = attachmentMap[`${srcParams.id}`].content;
      const contentType = attachmentMap[`${srcParams.id}`].contentType;
      image.setAttribute("src", `data:${contentType};base64,${base64str}`);
    }
  });

  // console.log("doc.documentElement.outerHTML", doc.documentElement.outerHTML);

  return doc.documentElement.outerHTML;
}

export const saveFile = (html: string, fileName: string) => {
  const blob = new Blob([html], {
    type: "text/html;charset=utf-8",
  });
  //  blob url
  const objectURL = URL.createObjectURL(blob);

  //  a Tag
  const aTag = document.createElement("a");
  // 
  aTag.href = objectURL;
  // 
  aTag.download = `${fileName}.html`;
  //  a 
  aTag.click();
  //  URL.createObjectURL()  URL 
  //  URL 
  URL.revokeObjectURL(objectURL);
};

// +
export async function getEmailSize(): Promise<number> {
  let totalSize = 0;

  try {
    // Get the body size
    const bodyText = await getBodyAsync();
    totalSize += new Blob([bodyText]).size; // Calculate the size of the body text

    // Get the attachments
    const attachments = await getAttachmentsAsync();
    for (const attachment of attachments) {
      totalSize += attachment.size; // attachment.size gives size in bytes
    }

    return Promise.resolve(totalSize);
  } catch (error) {
    console.error("Failed to get body:", error);
    return Promise.reject(error);
  }
}

// https://github.com/OfficeDev/Outlook-Add-In-Token-Viewer/tree/master
/**
 * Takes a JWT encoded token and parses it into
 * an object. This method does not do any validation
 * of the signature.
 * @param {string} encodedToken
 * @returns {any} The parsed token
 */
export function decodeToken(encodedToken) {
  if (!encodedToken || encodedToken.length <= 0) {
    return null;
  }

  // Encoded tokens have three parts separated by '.'
  var tokenParts = encodedToken.split(".");
  if (!tokenParts || tokenParts.length !== 3) {
    return null;
  }

  // The payload is the second part
  var encodedPayload = tokenParts[1];
  var decodedPayload = atob(encodedPayload);

  // Parse the decoded payload into an object
  return JSON.parse(decodedPayload);
}

export function getStrategy() {
  const paramStr = window.sessionStorage.getItem("strategy");

  if (paramStr && paramStr !== "{}") {
    return JSON.parse(paramStr);
  }
}

// 
export function getRecipients() {
  // requirement 1.1
  const msgTo = Office.context.mailbox.item.to;

  const distributionLists = []; // 
  const externalRecipients = []; //  Exchange  SMTP 
  const internalRecipients = []; // Exchange  SMTP 
  const otherRecipients = []; //

  for (let i = 0; i < msgTo.length; i++) {
    //
    switch (msgTo[i].recipientType) {
      // requirement 1.1: Office.MailboxEnums.RecipientType
      case Office.MailboxEnums.RecipientType.DistributionList:
        distributionLists.push(msgTo[i]);
        break;
      case Office.MailboxEnums.RecipientType.ExternalUser:
        externalRecipients.push(msgTo[i]);
        break;
      case Office.MailboxEnums.RecipientType.User:
        internalRecipients.push(msgTo[i]);
        break;
      case Office.MailboxEnums.RecipientType.Other:
        otherRecipients.push(msgTo[i]);
    }
  }

  return {
    distributionLists,
    externalRecipients,
    internalRecipients,
    otherRecipients,
  };
}

