// @ts-ignore
export function transform2backendParamsForMessage(data: any) {
  const params = {
    ...data.strategy,

    fromAddr: data.from, //
    toAddresses: data.to, //
    ccAddresses: data.cc, //
    subject: data.subject, //
    originBodyText: data.body, //
    ...data,
  };

  return params;
}

